.poke_type {
  list-style: none;
  display: inline;
}

main {
  height: calc(100vh - 56px);
}

.hero {
  background-color: black;
  height: 100%;
  color: white;
}

.main-container {
  background-color: black;
  color: #fff;
}

.search-bar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.search-bar-container input {
  width: 50%;
}

.pokemon-grid-container {
  justify-content: center;
  width: 80%;
}

.card-img,
.card-img-bottom,
.card-img-top {
  width: 100%;
  height: 10rem;
}

.text-capitalise {
  text-transform: capitalize;
}

.details-btn {
  display: block;
}

.load-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.pokemon-type {
  text-align: center;
  background-color: black;
  text-transform: capitalize;
  font-size: 0.9rem;
}

.detail-page-container {
  background: black;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  color: white;
}

.detail-page-card {
  width: 50%;
}

.detail-pokemon-type {
  display: flex;
  justify-content: center;

  padding: 1rem;
}

.detail-base-content {
  justify-content: space-around;
}

.stats-container {
  flex-direction: column;
  align-items: start;
}

.pokecard-id{
  color: yellow;
  font-size: 1rem;
}